import React, { useEffect, useState } from "react";
import { MapContainer, TileLayer, useMap } from "react-leaflet";
import "leaflet/dist/leaflet.css";
import L from "leaflet";
import "leaflet-control-geocoder";
import "./styles.css";

const defaultCenter = [41.7151, 44.8271]; //  

const SearchBox = ({ setMarkerPosition }) => {
  const map = useMap();

  useEffect(() => {
    const geocoder = L.Control.Geocoder.nominatim();
    const control = L.Control.geocoder({
      defaultMarkGeocode: false,
      geocoder,
      placeholder: "Search...",
    }).on("markgeocode", function (e) {
      const { center } = e.geocode;
      setMarkerPosition(center);
      map.setView(center, 13);
    });

    control.addTo(map).setPosition("topright"); //     

    return () => {
      map.removeControl(control); //  unmount-   
    };
  }, [map, setMarkerPosition]);

  return null;
};

const App = () => {
  const [markerPosition, setMarkerPosition] = useState(defaultCenter);

  return (
    <div className="map-container">
      <MapContainer center={defaultCenter} zoom={13} className="leaflet-map">
        <TileLayer
          url="https://{s}.tile.openstreetmap.org/{z}/{x}/{y}.png"
          attribution='&copy; <a href="https://www.openstreetmap.org/copyright">OpenStreetMap</a> contributors'
        />
        <SearchBox setMarkerPosition={setMarkerPosition} />
      </MapContainer>
    </div>
  );
};

export default App;
